import React from "react";
import MuiThemeProvider from "material-ui/styles/MuiThemeProvider";
import AppBar from "material-ui/AppBar";
import { List, ListItem } from "material-ui/List";
import RaisedButton from "material-ui/RaisedButton";

class Continue extends React.Component {
  continue = e => {
    e.preventDefault();
    this.props.nextStep();
  };

  prev = e => {
    e.preventDefault();
    this.props.prevStep();
  };

  render() {
    const { values } = this.props;
    return (
      <MuiThemeProvider>
        <>
          <AppBar title="Confirm User Data" />

          <List>
            <ListItem
              primaryText="First Name"
              secondaryText={values.firstName}
            />
            <ListItem primaryText="Last Name" secondaryText={values.lastName} />
            <ListItem primaryText="Email" secondaryText={values.email} />
            <ListItem primaryText="Gender" secondaryText={values.gender} />
            <ListItem primaryText="City" secondaryText={values.city} />
            <ListItem primaryText="Bio" secondaryText={values.bio} />
          </List>

          <RaisedButton
            label="Back"
            primary={true}
            onClick={this.prev}
            style={styles}
          />

          <RaisedButton
            label="Confirm & Continue"
            primary={true}
            onClick={this.continue}
            style={styles}
          />
        </>
      </MuiThemeProvider>
    );
  }
}

const styles = {
  margin: 15
};

export default Continue;
