import React from "react";
import FormUserDetail from "./FormUserDetail";
import FormPersonalDetail from "./FormPersonalDetail";
import Confirm from "./Confirm";
import Success from "./Success";

class UserForm extends React.Component {
  state = {
    step: 1,
    firstName: "",
    lastName: "",
    email: "",
    gender: "",
    city: "",
    bio: ""
  };

  // to get to the next step
  nextStep = () => {
    const { step } = this.state;
    this.setState({
      step: step + 1
    });
  };

  // to get to the prev step
  prevStep = () => {
    const { step } = this.state;
    this.setState({
      step: step - 1
    });
  };

  //handle fields change
  handleChange = event => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
  };

  render() {
    const { step } = this.state;
    const { firstName, lastName, email, gender, city, bio } = this.state;
    const values = { firstName, lastName, email, gender, city, bio };

    switch (step) {
      case 1:
        return (
          <FormUserDetail
            values={values}
            nextStep={this.nextStep}
            handleChange={this.handleChange}
          />
        );

      case 2:
        return (
          <FormPersonalDetail
            values={values}
            nextStep={this.nextStep}
            prevStep={this.prevStep}
            handleChange={this.handleChange}
          />
        );

      case 3:
        return (
          <Confirm
            values={values}
            nextStep={this.nextStep}
            prevStep={this.prevStep}
          />
        );

      case 4:
        return <Success />;

      default:
        return null;
    }
  }
}

export default UserForm;
